@charset "UTF-8";
/*Clearfix - чтобы плавающие блоки занимали место в родительских*/
.clearfix::before, .clearfix::after {
  content: " ";
  display: table; }

.clearfix::after {
  clear: both; }

:root {
  box-sizing: border-box;
  font-size: calc( 0.00208 * 100vw + 13px); }

*,
*::before,
*::after {
  box-sizing: inherit; }

#root {
  margin: 0 auto;
  padding: 0;
  width: 83%;
  max-width: 1440px; }

body {
  margin: 0;
  padding: 0;
  padding-bottom: 5em;
  font-family: 'Rubik', Helvetica, Arial, sans-serif;
  color: #000; }
  body select {
    background-color: #fff; }
    body select option[value="not selected"][disabled] {
      display: none; }
  body label {
    display: block;
    padding: 0.4em 0; }
  body input, body select {
    font-size: 1em;
    padding: 0.4em 0;
    border: none;
    border-bottom: 1px solid #000;
    width: 100%;
    vertical-align: top;
    height: 2.25rem; }
  body input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none; }
  body input:active, body input:focus,
  body button:active, body button:focus {
    outline: none; }
  body input::-moz-focus-inner, body button::-moz-focus-inner {
    outline: none; }
  body select:focus {
    outline: none; }
  body button {
    cursor: pointer;
    font-size: 1em;
    display: block;
    background-color: #000;
    padding: 1em 5em;
    color: #fff;
    border: none;
    font-family: 'Roboto', Helvetica, Arial, sans-serif;
    margin: 0 auto; }
  body .lightBtn {
    background-color: #fff;
    color: #A3A6A9;
    padding: 0; }
  body h1 {
    font-size: 4em; }
  body h2 {
    font-size: 2em; }

.title {
  margin-bottom: 4.625rem; }

.filterBlock {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr;
  grid-gap: 1.6%;
  margin-bottom: 2rem; }
  .filterBlock button {
    float: right;
    height: 2.25rem; }
    .filterBlock button img {
      margin-left: 0.75rem; }
  .filterBlock .clearType {
    padding-top: calc(1.15rem + 0.8rem); }

.positionsList {
  font-family: 'Roboto', sans-serif;
  margin-bottom: 4.125rem; }

.position {
  display: grid;
  grid-template-columns: 13fr 24fr;
  grid-gap: 7.8%;
  padding: 1.25rem;
  margin-bottom: 1.5rem;
  border: solid 1px #000;
  margin-bottom: 1.5rem; }
  .position p {
    display: inline-block; }
  .position a:link, .position a:visited, .position a:hover, .position a:active {
    color: #000;
    text-decoration: none; }
  .position a:hover {
    text-decoration: underline; }
  .position h2 {
    font-size: 1.5em; }
  .position .mainInfo {
    position: relative;
    margin-bottom: 1.5em; }
    .position .mainInfo h2, .position .mainInfo p {
      margin: 0.3em 0; }
    .position .mainInfo button {
      position: absolute;
      z-index: 100;
      left: 0;
      width: 100%;
      text-align: center;
      bottom: -1.5em; }
  .position .closedInfo {
    margin-bottom: 0em;
    height: 18em;
    overflow: hidden;
    background: #fff; }
    .position .closedInfo:after {
      content: '';
      width: 100%;
      height: 6em;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 10;
      background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, white 50%); }
    .position .closedInfo button {
      bottom: 0; }
  .position .mainInfo > h2:first-child {
    font-size: 2em; }
  .position .leftColumn img {
    margin-top: 1.25rem;
    margin-bottom: 3.56rem;
    margin-top: 0;
    max-height: 6rem;
    max-width: 100%;
    display: block; }
  .position .leftColumn p {
    font-family: 'Rubik', Helvetica, Arial, sans-serif;
    display: block;
    margin: 0; }
    .position .leftColumn p span {
      color: #545454; }
  .position .leftColumn p + p {
    margin-top: 1rem; }

.requestContact {
  display: grid;
  grid-template-columns: 2fr 3fr;
  grid-gap: 22%; }
  .requestContact .sendInfo {
    margin-top: 2.7em;
    font-size: 14px; }
    .requestContact .sendInfo p {
      margin-top: 0; }
  .requestContact input {
    padding-left: 0.5em;
    margin-bottom: 2rem; }
  .requestContact input + p {
    color: #fc4a4a;
    margin-top: 0.3em;
    margin-bottom: 2rem; }
  .requestContact .invalid {
    margin-bottom: 0;
    border-radius: 5px;
    border: solid 1px #fc4a4a; }
  .requestContact button {
    margin: 0;
    display: block; }
  .requestContact .dotted {
    border-bottom: 1px dotted #000;
    text-decoration: none; }
  .requestContact .contactInfo {
    display: table-cell;
    vertical-align: middle; }
  .requestContact h4 {
    font-size: 1.125em;
    font-weight: normal;
    margin: 0; }

/**   Мобильные устройства  **/
@media screen and (max-width: 1000px) {
  :root {
    font-size: 16px; }
  #root {
    width: 85%; }
  body h1 {
    font-size: 2rem; }
  body h2 {
    font-size: 1.375rem; }
  body p, body a {
    font-size: 1rem; }
  body button {
    padding: 1rem 30%;
    text-align: center; }
  .title {
    margin-bottom: 2rem; }
  .title:first {
    margin-top: 5rem;
    margin-bottom: 2.375rem; }
  .filterBlock {
    display: block; }
    .filterBlock .clearType {
      padding: 0; }
    .filterBlock button {
      float: none;
      margin: 0;
      margin-bottom: 1rem; }
    .filterBlock select, .filterBlock input {
      margin-bottom: 1rem; }
  .positionsList {
    margin-bottom: 4rem; }
  .position {
    display: block; }
    .position h2 {
      font-size: 1rem; }
    .position .mainInfo > h2:first-child {
      font-size: 1.375rem;
      margin-top: 1rem; }
    .position .leftColumn img {
      max-width: 100%;
      max-height: 3.75rem;
      margin-bottom: 2.375rem; }
  .requestContact {
    display: block; }
    .requestContact .rightColumn {
      margin-top: 2rem; } }

